body {
  font-family: "AkzidenzGroteskPro-Cn", "Arial", sans-serif;
  font-weight: 300;
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

h1,
h2,
h3 {
  font-family: "Lady Marmalade", "Times", serif;


  font-family: "AkzidenzGroteskPro-Cn", "Times", serif;
}

img {
  max-width: 100%;
  height: auto;
  margin: 0;
}

ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
}

input[name="toggle"] {
  display: none;
}
