.logo {
  position: relative;
  display: block;
  width: 313px;
  height: 72px;
  cursor: pointer;
  margin-left: auto;

  @media (max-width: 1600px) {
    width: 250px;
    height: 55px;
  }

  @media (max-width: 1280px) {
    width: 209px;
    height: 48px;
  }
}

.logo img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
}
