.main-nav {
  position: relative;
}

.main-nav--closed {

}

.main-nav__wrap {
  position: absolute;
  display: none;
  width: 100%;
  left: 50%;
  transform: translateX(-50%);
  background-color: var(--basic-white);
  z-index: 10;
}

.main-nav__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 8.4vw;
  padding-top: 42px;
  padding-left: 267px;
  padding-bottom: 42px;
}

.main-nav__list a {
  text-decoration: none;
}


/*  Сворачивание Меню  */

.main-nav__toggle {
  position: absolute;
  display: block;
  top: -75px;
  left: 207px;
  width: 28px;
  height: 16px;
  background-color: transparent;
  padding: 0;
  border: none;
  outline: none;
  cursor: pointer;
  z-index: 15;

  outline: 2px solid red;

  @media (max-width: 1600px) {
    width: 21px;
    height: 12px;
    top: -65px;
    left: 160px;
  }

  @media (max-width: 1280px) {
    width: 18px;
    height: 10px;
    top: -60px;
    left: 130px;
  }
}

.main-nav--closed .main-nav__toggle::before {
  display: block;
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 28px;
  height: 2px;
  background-color: var(--back-main);
  box-shadow: 0 8px 0 0 var(--back-main), 0 16px 0 0 var(--back-main);

  @media (max-width: 1600px) {
    width: 21px;
    box-shadow: 0 6px 0 0 var(--back-main), 0 12px 0 0 var(--back-main);
  }

  @media (max-width: 1280px) {
    width: 18px;
    height: 1px;
    box-shadow: 0 5px 0 0 var(--back-main), 0 10px 0 0 var(--back-main);
  }
}

.main-nav--closed .main-nav__toggle:hover::before,
.main-nav--closed .main-nav__toggle:focus::before {
  opacity: 0.5;
}

.main-nav--closed .main-nav__toggle:active::before {
  opacity: 0.2;
}

.main-nav--opened .main-nav__toggle {
  top: 30px;
  background-color: transparent;
}

.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 0px;
  left: 0;
  width: 40px;
  height: 2px;
  background-color: var(--back-basic);
}

.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}

.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}

.main-nav--opened .main-nav__toggle:hover::before,
.main-nav--opened .main-nav__toggle:hover::after,
.main-nav--opened .main-nav__toggle:focus::before,
.main-nav--opened .main-nav__toggle:focus::after {
  opacity: 0.5;
}

.main-nav--opened .main-nav__toggle:active::before,
.main-nav--opened .main-nav__toggle:active::after {
  opacity: 0.2;
}
