.copyright {
  display: flex;
  justify-content: space-between;
  font-size: 25px;
  font-weight: 100;
  color: var(--basic-white);

  @media (max-width: 1600px) {
    font-size: 20px;
  }

  @media (max-width: 1280px) {
    font-size: 17px;
  }
}

.copyright p {
  margin: 0;
}

.copyright__link {
  color: var(--basic-white);
  margin-left: auto;
}
