.nav-footer {
  display: grid;
  grid-template-columns: max-content repeat(3, 1fr);
  justify-content: space-between;
  justify-items: center;
  row-gap: 30px;
  padding-top: 55px;
  padding-bottom: 62px;

  @media (max-width: 1600px) {
    padding-top: 45px;
    padding-bottom: 50px;
  }

  @media (max-width: 1280px) {
    padding-top: 37px;
    padding-bottom: 39px;
  }
}

.nav-footer__logo {
  position: relative;
  display: block;
  width: 206px;
  height: 29px;
  margin-bottom: 27px;

  @media (max-width: 1600px) {
    width: 165px;
    height: 23px;
    margin-bottom: 21px;
  }

  @media (max-width: 1280px) {
    width: 137px;
    height: 19px;
    margin-bottom: 18px;
  }
}

.nav-footer__logo img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
}

.nav-footer__item:nth-child(1) {
  width: 280px;

  @media (max-width: 1280px) {
    width: 200px;
  }
}

.nav-footer h4 {
  font-family: inherit;
  font-size: 34px;
  line-height: 35px;
  font-weight: 300;
  text-transform: uppercase;
  color: var(--back-red);
  margin: 0;
  margin-bottom: 27px;

  @media (max-width: 1600px) {
    font-size: 27px;
    line-height: 28px;
    margin-bottom: 21px;
  }

  @media (max-width: 1280px) {
    font-size: 23px;
    line-height: 24px;
    margin-bottom: 18px;
  }
}

.nav-footer p,
.nav-footer a {
  font-family: inherit;
  font-size: 34px;
  line-height: 35px;
  font-weight: 100;

  @media (max-width: 1600px) {
    font-size: 27px;
    line-height: 28px;
  }

  @media (max-width: 1280px) {
    font-size: 23px;
    line-height: 24px;
  }
}

.nav-footer p {
  margin: 0;
}

.nav-footer a {
  color: var(--basic-blue);
}

.nav-footer li {
  margin-bottom: 16px;

  @media (max-width: 1600px) {
    margin-bottom: 14px;
  }

  @media (max-width: 1280px) {
    margin-bottom: 12px;
  }
}

.nav-footer__map {
  display: block;
  margin-top: 42px;

  @media (max-width: 1600px) {
    margin-top: 34px;
  }

  @media (max-width: 1280px) {
    margin-top: 28px;
  }
}

.nav-footer__info {

}

.nav-footer__title {

}

.nav-footer__item--contact p,
.nav-footer__item--contact a, {
  font-size: 25px;
  line-height: 28px;
  font-weight: 300;
  text-transform: uppercase;
  color: var(--back-main);
  text-decoration: none;
  margin-bottom: 10px;

  @media (max-width: 1600px) {
    font-size: 20px;
    line-height: 23px;
  }

  @media (max-width: 1280px) {
    font-size: 17px;
    line-height: 19px;
  }
}

a.nav-footer__mail {
  display: block;
  color: var(--basic-blue);
  text-decoration: underline;
  margin-top: 15px;
}

.nav-footer__form {
  margin-top: 45px;
}
