.gallery-slider {
  margin-top: 30px;

  user-select: none;

  outline: 2px solid blue;
}

.gallery-slider__list.swiper-wrapper {
  position: relative;

  background: red;


  outline: 2px solid green;
}

.gallery-slider__item.swiper-slide {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  outline: 2px solid orange;
}

.gallery-slider__item.card::after {
  top: -23px;
  right: 10px;
  z-index: 20;
}

.gallery-slider .msfavorites-text-add,
.gallery-slider .msfavorites-text-remove {
  position: absolute;
  display: flex;
  width: 40px;
  height: 37px;
  top: 465px;
  right: 30px;
  font-size: 0;
  background-repeat: no-repeat;
  background-size: 100% auto;
  cursor: pointer;

  @media (max-width: 1600px) {
    width: 32px;
    height: 30px;
    top: 372px;
    right: 0px;
  }

  @media (max-width: 1280px) {
    width: 27px;
    height: 25px;
    top: 310px;
    left: 258px;
  }
}

.gallery-slider .msfavorites-text-add {
  background-image: url("../img/icon-heart.png");
}

.gallery-slider .msfavorites-text-remove {
  display: none;
  background-image: url("../img/icon-heart-active.png");
}

.gallery-slider__toggle {
  position: absolute;
  display: block;
  width: 31px;
  height: 45px;
  top: 209px;
  background: url("../img/arrow-slider.png");
  background-repeat: no-repeat;
  background-size: 100% auto;
  cursor: pointer;
  z-index: 15;

  @media (max-width: 1600px) {
    width: 25px;
    height: 36px;
    top: 169px;
  }

  @media (max-width: 1280px) {
    width: 20px;
    height: 30px;
    top: 140px;
  }
}

.gallery-slider__toggle--prev {
  left: 0;
  transform: scaleX(-1);
}

.gallery-slider__toggle--next {
  right: 0;
}
