.about-us {
  color: var(--basic-white);
}

.about-us__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  column-gap: 80px;
  font-size: 34px;
  font-weight: 100;
  margin-top: 53px;

  @media (max-width: 1600px) {
    column-gap: 64px;
    font-size: 26px;
    line-height: 29px;
  }

  @media (max-width: 1280px) {
    column-gap: 55px;
    font-size: 22px;
    line-height: 25px;
  }
}

.about-us__list a {
  color: var(--basic-white);
}

.about-us h2 {
  font-size: 59px;
  line-height: 60px;
  text-align: center;
  margin: 0;

  @media (max-width: 1600px) {
    font-size: 47px;
    line-height: 50px;
  }

  @media (max-width: 1280px) {
    font-size: 39px;
    line-height: 40px;
  }
}
