.container {
  max-width: 1920px;
  padding-left: 200px;
  padding-right: 200px;
  margin: 0 auto;

  outline: 1px solid red;

  @media (max-width: 1600px) {
    max-width: 1600px;
    padding-left: 160px;
    padding-right: 160px;
  }

  @media (max-width: 1280px) {
    max-width: 1280px;
    padding-left: 130px;
    padding-right: 130px;
  }

  @media (max-width: 992px) {
    max-width: 992px;

  }

  @media (max-width: 767px) {
    max-width: none;
  }
}
