.page-header {
  padding-top: 13px;
}

.page-header__top {
  font-family: inherit;
  font-size: 25px;
  text-transform: uppercase;

  @media (max-width: 1600px) {
    font-size: 20px;
  }

  @media (max-width: 1280px) {
    font-size: 17px;
  }
}

.page-header__top a {
  color: var(--back-main);
}

