.gallery-goods {

}

.gallery-goods__list {
  padding-top: 50px;
  padding-left: 40px;
  padding-bottom: 62px;

  @media (max-width: 1600px) {
    padding-top: 41px;
    padding-left: 32px;
    padding-bottom: 50px;
  }

  @media (max-width: 1280px) {
    padding-top: 34px;
    padding-left: 27px;
    padding-bottom: 42px;
  }
}

.gallery-goods h3 {
  font-size: 2.6vw;
  margin: 0;

  @media (max-width: 1600px) {
    font-size: 2.5vw;
    line-height: 42px;
  }

  @media (max-width: 1280px) {
    font-size: 2.66vw;
    line-height: 36px;
  }
}

.gallery-goods a {
  text-decoration: none;
}
