.gallery-cards {
  display: grid;
  grid-template-columns: repeat(4, 14.74vw);
  column-gap: 4.95vw;
  text-align: center;

  @media (max-width: 1600px) {
    grid-template-columns: repeat(4, 14.38vw);
    column-gap: 4.75vw;
  }

  @media (max-width: 1280px) {
    grid-template-columns: repeat(4, 15vw);
    column-gap: 5vw;
  }
}

.gallery-cards h3 {
  margin-bottom: 30px;

  @media (max-width: 1600px) {
    margin-bottom: 26px;
  }

  @media (max-width: 1280px) {
    margin-bottom: 21px;
  }
}

.gallery-cards__item {
  display: flex;

  outline: 2px solid blue;
}

.gallery-cards__item.card::after {
  top: 60px;
  right: -35px;
  z-index: 20;
}

.gallery-cards p {
  position: relative;
  margin: 0;
  margin-top: 80px;

  @media (max-width: 1600px) {
    margin-top: 67px;
  }

  @media (max-width: 1280px) {
    margin-top: 55px;
  }
}

.gallery-cards p::before {
  position: absolute;
  content: "";
  width: 166px;
  height: 17px;
  top: -20px;
  left: 50%;
  transform: translateX(-50%);
  background-image: url("../img/vignette-small.png");
  background-repeat: no-repeat;
  background-size: 100% auto;

  @media (max-width: 1600px) {
    width: 133px;
    height: 14px;
  }

  @media (max-width: 1280px) {
    width: 110px;
    height: 12px;
  }
}
