.our-works__list {
  display: grid;
  grid-template-columns: repeat(7, max-content);
  column-gap: 1.67vw;
  row-gap: 32px;
  margin-top: 57px;

  @media (max-width: 1600px) {
    column-gap: 1.56vw;
    row-gap: 25px;
    margin-top: 46px;
  }

  @media (max-width: 1280px) {
    column-gap: 1.64vw;
    row-gap: 21px;
    margin-top: 38px;
  }
}

.our-works h2 {
  font-size: 59px;
  line-height: 60px;
  color: var(--basic-blue);
  margin: 0;

  @media (max-width: 1600px) {
    font-size: 47px;
    line-height: 50px;
  }

  @media (max-width: 1280px) {
    font-size: 39px;
    line-height: 40px;
  }
}

.our-works__list li {
  position: relative;
  width: 10.42vw;
  height: 10.42vw;

  @media (max-width: 1600px) {
    width: 10vw;
    height: 10vw;
  }

  @media (max-width: 1280px) {
    width: 10.42vw;
    height: 10.42vw;
  }
}

.our-works__list img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
}
