.offers {
  color: var(--basic-white);
}

.offers h2 {
  font-size: 59px;
  line-height: 60px;
  text-align: center;
  margin: 0;

  @media (max-width: 1600px) {
    font-size: 47px;
    line-height: 50px;
  }

  @media (max-width: 1280px) {
    font-size: 39px;
    line-height: 40px;
  }
}

.offers__list {
  display: grid;
  grid-template-columns: repeat(2, minmax(auto, 710px));
  column-gap: 92px;
  margin-top: 46px;

  @media (max-width: 1600px) {
    column-gap: 76px;
    margin-top: 38px;
  }

  @media (max-width: 1280px) {
    column-gap: 64px;
    margin-top: 32px;
  }
}

.offers__item > div {
  display: grid;
  grid-auto-flow: row;
  height: 72%;
  border-left: 3px solid var(--basic-white);
  margin-bottom: 58px;

  @media (max-width: 1600px) {
    margin-bottom: 48px;
  }

  @media (max-width: 1280px) {
    margin-botyom: 38px;
  }
}

.offers__item p {
  font-size: 34px;
  line-height: 34px;
  font-weight: 100;
  margin: 0;
  margin-bottom: 40px;
  padding-left: 40px;

  @media (max-width: 1600px) {
    font-size: 27px;
    line-height: 29px;
    padding-left: 30px;
    margin-bottom: 34px;
  }

  @media (max-width: 1280px) {
    font-size: 23px;
    line-height: 25px;
    padding-left: 26px;
    margin-bottom: 28px;
  }
}

.offers__item p:first-child {
  padding-top: 16px;

  @media (max-width: 1600px) {
    padding-top: 14px;
  }

  @media (max-width: 1280px) {
    padding-top: 10px;
  }
}

.offers__item p:last-child {
  padding-bottom: 16px;
  margin-bottom: 0;

  @media (max-width: 1600px) {
    padding-bottom: 14px;
  }

  @media (max-width: 1280px) {
    padding-bottom: 10px;
  }
}

.offers__list a {
  color: var(--basic-white);
}

.offers__link {
  position: relative;
  font-family: "Lady Marmalade", "Times", serif;
  font-size: 42px;
  text-transform: uppercase;
  color: var(--basic-white);
  text-decoration: none;
  padding-left: 40px;


  font-family: "AkzidenzGroteskPro-Cn", "Times", serif;

  @media (max-width: 1600px) {
    font-size: 34px;
    padding-left: 30px;
  }

  @media (max-width: 1280px) {
    font-size: 28px;
    padding-left: 25px;
  }
}

.offers__link::after {
  position: absolute;
  content: "";
  width: 19px;
  height: 28px;
  top: 50%;
  transform: translateY(-50%);
  right: -32px;
  background-image: url("../img/arrow-white.png");
  background-repeat: no-repeat;
}

