.card {
  position: relative;
}

.card::after {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  content: "";
  width: 115px;
  height: 115px;
  font-family: inherit;
  font-size: 21px;
  text-transform: uppercase;
  letter-spacing: -0.5px;
  text-align: center;
  color: var(--basic-white);
  border-radius: 50%;
  z-index: 20;
}

.card--new::after {
  content: "новинка";
  background-color: var(--basic-blue);
}

.card--exclusive::after {
  content: "эксклюзив";
  background-color: var(--back-red);
}

.card--hit::after {
  content: "хит продаж";
  background-color: var(--basic-orange);
}
