.form-search {
  position: relative;
  max-width: 300px;
  display: flex;
  align-items: center;
  padding: 7px;
  border: 0.7px solid var(--basic-blue);

  @media (max-width: 767px) {
    width: 100%;
    margin: 0 auto;
    margin-top: 7vw;
  }
}

.form-search input[type="search"] {
  width: 100%;
  font-size: 15px;
  color: var(--basic-blue);
  padding: 0 0 0 6px;
  background: transparent;
  border: 0;
  outline: 0;
  cursor: pointer;
  pointer-events: auto;
  padding-right: 10px;
}

.form-search input[type="search"]::-webkit-input-placeholder {
  font-size: 14px;
  color: rgba(255, 255, 255, 0.3);
}

.form-search:focus-within input[type="search"],
.form-search input[type="search"]:valid {

}

.form-search__btn {
  width: 18px;
  height: 18px;
  background: url("../img/loop.png") transparent;
  background-repeat: no-repeat;
  background-size: content;
  border: none;
  cursor: pointer;
  padding: 0;
}

.form-search input[type="search"]::-webkit-input-placeholder {
  visibility: hidden;
  font-family: inherit;
  color: rgba(255, 255, 255, 0.3);
}

.form-search:focus input[type="search"]::-webkit-input-placeholder,
.form-search:hover input[type="search"]::-webkit-input-placeholder {
  visibility: visible;
}

.form-search:focus input[type="search"],
.form-search:hover input[type="search"] {
  visibility: visible;
}
