@mixin _1600 {
    @media (max-width: 1600px){
        @content;
    }
}

@mixin _1280 {
  @media (max-width: 1280px) {
    @content;
  }
}

@mixin _1080 {
  @media (max-width: 1080px) {
    @content;
  }
}

@mixin _992 {
  @media (max-width: 992px) {
    @content;
  }
}

@mixin _767 {
  @media (max-width: 767px) {
    @content;
  }
}

@mixin _680 {
  @media (max-width: 680px) {
    @content;
  }
}

@mixin _580 {
  @media (max-width: 580px) {
    @content;
  }
}

@mixin _480 {
  @media (max-width: 480px) {
    @content;
  }
}

/*  Viewports  */

$tablet-width: 767px;
$desktop-width: 1280px;
$mobile-width-only: 580px;
$retina-dpi: 144dpi;
$retina-dppx: 1.5dppx;

/*  Colors  */

:root {
  --back-main: #000000;
  --back-red: #c52f43;
  --basic-white: #ffffff;
  --basic-blue: #156a9e;
  --basic-light-blue: #e6f3ff;
  --basic-orange: #f16f00;
  --nav-main-size: 25px;
}
