.gallery-info {
  max-width: 458px;
  text-decoration: none;

  @media (max-width: 1600px) {
    max-width: 365px;
  }

  @media (max-width: 1280px) {
    max-width: 305px;
  }
}

.gallery-info__img {
  position: relative;
  width: 458px;
  height: 458px;
  margin: 0;

  outline: 1px solid red;

  @media (max-width: 1600px) {
    width: 365px;
    height: 365px;
  }

  @media (max-width: 1280px) {
    width: 305px;
    height: 305px;
  }
}

.gallery-info img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
}

.gallery-info__desc {
  width: 458px;
  text-transform: uppercase;
  text-align: center;
  margin-top: 55px;

  @media (max-width: 1600px) {
    width: 365px;
  }

  @media (max-width: 1280px) {
    width: 305px;
  }
}

.gallery-info__desc p {
  font-size: 25px;
  color: var(--back-main);
  margin: 0;

  @media (max-width: 1600px) {
    font-size: 20px;
  }

  @media (max-width: 1280px) {
    font-size: 17px;
  }
}

.gallery-info__desc span {
  position: relative;
  font-size: 17px;
  color: var(--basic-blue);

  @media (max-width: 1600px) {
    font-size: 14px;
  }

  @media (max-width: 1280px) {
    font-size: 12px;
  }
}

.gallery-info__desc span::after {
  position: absolute;
  content: "";
  width: 166px;
  height: 17px;
  top: 30px;
  left: 50%;
  transform: translateX(-50%);
  background-image: url("../img/vignette-small.png");
  background-repeat: no-repeat;
  background-size: 100% auto;

  @media (max-width: 1600px) {
    width: 133px;
    height: 14px;
  }

  @media (max-width: 1280px) {
    width: 110px;
    height: 12px;
  }
}

p.gallery-info__price {
  margin-top: 40px;

  @media (max-width: 1280px) {
    margin-top: 34px;
  }
}

.gallery-info__name {
  display: grid;
  grid-auto-flow: row;
  min-height: 93px;
}
