.nav-inner {
  margin-top: 54px;

  @media (max-width: 1600px) {
    margin-top: 43px;
  }

  @media (max-width: 1280px) {
    margin-top: 34px;
  }
}

.nav-inner li {
  margin-bottom: 10px;
}

.nav-inner a {
  color: var(--basic-blue);
  text-decoration: underline;

  &:hover {
    color: var(--back-red);
  }
}


