.top-list {
  display: flex;
  font-size: 25px;
  text-transform: uppercase;
  margin-left: 70px;

  @media (max-width: 1600px) {
    font-size: 20px;
  }

  @media (max-width: 1280px) {
    font-size: 17px;
  }
}

.top-list li {
  margin-right: 70px;

  @media (max-width: 1600px) {
    margin-right: 55px;
  }

  @media (max-width: 1280px) {
    margin-right: 48px;
  }
}

.top-list li:last-child {
  margin-right: 0;
}

.top-list a {
  color: var(--back-main);
  text-decoration: none;

  &:hover {
    color: var(--back-red);
  }
}

