.back-wrapper {
  width: 100%;
}

.back-wrapper--about {
  background-color: var(--basic-blue);
  padding-top: 45px;
  padding-bottom: 60px;

  @media (max-width: 1600px) {
    padding-top: 40px;
    padding-bottom: 50px;
  }

  @media (max-width: 1280px) {
    padding-top: 35px;
    padding-bottom: 42px;
  }
}

.back-wrapper--works {
  background-color: var(--basic-light-blue);
  padding-top: 45px;
  padding-bottom: 74px;

  @media (max-width: 1600px) {
    padding-top: 40px;
    padding-bottom: 60px;
  }

  @media (max-width: 1280px) {
    padding-top: 35px;
    padding-bottom: 50px;
  }
}

.back-wrapper--offer {
  background-color: var(--back-red);
  padding-top: 45px;
  padding-bottom: 50px;

  @media (max-width: 1600px) {
    padding-top: 34px;
    padding-bottom: 37px;
  }

  @media (max-width: 1280px) {
    padding-top: 28px;
    padding-bottom: 30px;
  }
}

.back-wrapper--footer {
  background-color: var(--basic-blue);
  padding-top: 37px;
  padding-bottom: 37px;

  @media (max-width: 1600px) {
    padding-top: 28px;
    padding-bottom: 28px;
  }

  @media (max-width: 1280px) {
    padding-top: 25px;
    padding-bottom: 25px;
  }
}
