.slider-main {

}

.slider-main__wrap {

}

.slider-main__list {

}

.slider-main__item {
  display: flex;
}

.slider-main__img {
  position: relative;
  width: 100%;
  height: 1323px;
  text-align: center;

  @media (max-width: 1600px) {
    height: 1058px;
  }

  @media (max-width: 1280px) {
    height: 882px;
  }
}

.slider-main__item img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  object-fit: cover;
  object-position: 50% 80%;
}

.slider-main__card {
  padding: 80px 74px 76px 105px;

  @media (max-width: 1600px) {
    padding: 63px 50px 62px 83px;
  }

  @media (max-width: 1280px) {
    padding: 4.14vw 50px 50px 5.63vw;
  }
}

.slider-main__toggle {
  position: absolute;
  display: block;
  width: 84px;
  height: 115px;
  top: 530px;
  right: 0;
  background: url("../img/arrow-white.png") rgba(197, 47, 67, 0.8);
  background-repeat: no-repeat;
  background-size: 30px 46px;
  background-position: 60% 50%;
  z-index: 10;
  cursor: pointer;

  @media (max-width: 1600px) {
    width: 67px;
    height: 92px;
    top: 410px;
    background-size: 25px 37px;
  }

  @media (max-width: 1280px) {
    width: 56px;
    height: 77px;
    top: 343px;
    background-size: 20px 31px;
  }
}
