.gallery {
  max-width: 1660px;
  padding-top: 40px;
  padding-bottom: 27px;
  margin: 0 auto;
}

.gallery__title {

}

.gallery__list {

}

.gallery__item {
  width: 458px;
}

.gallery h3 {
  font-size: 50px;
  line-height: 52px;
  color: var( --basic-blue);
  text-align: center;
  margin: 0;

  @media (max-width: 1600px) {
    font-size: 40px;
    line-height: 42px;
  }

  @media (max-width: 1280px) {
    font-size: 34px;
    line-height: 36px;
  }
}
