.card-info {
  position: absolute;
  width: 700px;
  min-height: 829px;
  font-size: 25px;
  text-transform: uppercase;
  color: var(--basic-white);
  top: 160px;
  left: 97px;

  @media (max-width: 1600px) {
    width: 558px;
    font-size: 20px;
    min-height: 663px;
    top: 130px;
    left: 78px;
  }

  @media (max-width: 1280px) {
    width: 36.25vw;
    min-height: 43.13vw;
    font-size: 17px;
    top: 107px;
    left: 63px;
  }
}

.card-info--red {
  background-color: rgba(197, 47, 67, 0.8);
}

.card-info--blue {
  background-color: rgba(21, 106, 158, 0.8);
}

.card-info--orange {
  background-color: rgba(241, 111, 0, 0.8);
}

.card-info__wrap {
  padding-left: 265px;
  margin-top: 107px;

  @media (max-width: 1600px) {
    padding-left: 215px;
    margin-top: 85px;
  }

  @media (max-width: 1280px) {
    padding-left: 14.06vw;
    margin-top: 72px;
  }
}

.card-info h2 {
  font-size: 59px;
  line-height: 60px;
  margin: 0;

  @media (max-width: 1600px) {
    font-size: 47px;
    line-height: 50px;
  }

  @media (max-width: 1280px) {
    font-size: 39px;
    line-height: 40px;
  }
}

.card-info__wrap p {
  position: relative;
  margin: 0;
  margin-bottom: 157px;

  @media (max-width: 1600px) {
    margin-bottom: 125px;
  }

  @media (max-width: 1280px) {
    margin-bottom: 103px;
  }
}

.card-info__wrap p:last-child {
  margin-bottom: 0;
}

.card-info__wrap p::before {
  position: absolute;
  content: "";
  width: 230px;
  height: 230px;
  top: -50px;
  left: -265px;
  background-repeat: no-repeat;
  background-size: 100% auto;

  @media (max-width: 1600px) {
    width: 185px;
    height: 185px;
    top: -40px;
    left: -215px;
  }

  @media (max-width: 1280px) {
    width: 11.95vw;
    height: 11.95vw;
    top: -33px;
    left: -14.06vw;
  }
}

.card-info__wrap p:nth-child(1)::before {
  background-image: url("../img/slider-main-flower-1.png");
}

.card-info__wrap p:nth-child(2)::before {
  background-image: url("../img/slider-main-flower-2.png");
}

.card-info__wrap a {
  display: block;
  color: var(--basic-white);
  margin-top: 35px;

  @media (max-width: 1600px) {
    margin-top: 27px;
  }

  @media (max-width: 1280px) {
    margin-top: 23px;
  }
}
