.header-user {
  display: grid;
  grid-template-columns: repeat(2, max-content);
  column-gap: 93px;
  justify-self: end;

  @media (max-width: 1600px) {
    column-gap: 75px;
  }

  @media (max-width: 1280px) {
    column-gap: 62px;
  }
}

.header-user__list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 35px;
  justify-content: end;
  align-items: center;
}

.header-user a {
  color: var(--back-main);
  text-decoration: none;
}

.header-user__basket {
  position: relative;
}

.header-user__basket span {
  position: absolute;
  width: 24px;
  height: 24px;
  top: 0;
  left: 12px;
  font-size: 17px;
  text-align: center;
  color: var(--basic-white);
  background-color: var(--back-red);
  border-radius: 50%;

  @media (max-width: 1600px) {
    width: 20px;
    height: 20px;
    font-size: 14px;
  }

  @media (max-width: 1280px) {
    width: 17px;
    height: 17px;
    font-size: 12px;
  }
}

.header-user__list button {
  width: 20px;
  height: 19px;
  background: url("../img/icon-heart-header.png") transparent;
  background-repeat: no-repeat;
  background-size: 100% auto;
  border: none;
  padding: 0;
  cursor: pointer;
}
